<script setup>
import { ref } from 'vue';
import Intro from './components/intro/Intro.vue'
import Calendar from './components/Calendar.vue'
import WeeklyPay from './components/WeeklyPay.vue'
import TotalPay from './components/TotalPay.vue'

const pay = ref(1000)
const date = ref(new Date())
let payArr = ref([])
const rate = 0.07
const totalPay = ref(0)
const totalBenefit = ref(0)

const weeks26 = () => {
  payArr.value = []
  totalPay.value = 0
  totalBenefit.value = 0
  let result = 0;
  for (let i = 1; i <= 26; i ++) {
    result += (pay.value * i);
    let dateStr = new Date(date.value)
    const payDate = new Date(dateStr.setDate(dateStr.getDate() + (7 * (i - 1))))

    payArr.value.push({ weekly: i, weeklyPay: pay.value * i, total: result, date: payDate })
  }
  payArr.value.forEach((pay) => {
    const startDay = new Date(pay.date)
    const lastDay = new Date(payArr.value[25].date)
    const endDay = new Date(lastDay.setDate(lastDay.getDate() + 7))
    const gap = endDay - startDay
    const dDay = Math.ceil(gap / (1000 * 60 * 60 * 24))
    const benefit = pay.weeklyPay * rate * (dDay/365)
    pay.benefit = benefit.toFixed(2)
    
    totalBenefit.value += Number(benefit)
  }) 
  totalPay.value = payArr.value[25].total
}

const emitData = (val1, val2) => {
  if (val1) pay.value = val1
  if (val2) date.value = val2
  weeks26();
}

</script>
<template>
  <section id="section">
    <Intro @emitData="emitData"/>
    <Calendar :payList="payArr"/>
    <WeeklyPay :payList="payArr"/>
    <TotalPay :totalPay="totalPay" :totalBenefit="totalBenefit"/>
  </section>
</template>
<style lang="scss">
@import './styles/variabled';
@import './styles/common';
@import './styles/index';
@import './styles/row';
</style>