<script setup>
  import { ref, defineEmits, watch, onMounted } from 'vue';
  import TabMenu from './TabMenu.vue';
  import DatePicker from '../DatePicker.vue';
  const emits = defineEmits(['emitData'])

  let tab
  const date = ref(new Date())
  const selectYear = ref(null)
  const selectMonth = ref(null)
  const selectDate = ref(null)
 
  const openDatePicker = ref(false)

  const emitTab = (val) => {
    tab = val
    emits('emitData', tab, date.value)    
  }
  const emitDate = (val) => {
    if (val.mark) date.value = new Date(val.mark)
    emits('emitData', tab, date.value)
  }

  const getSelectDate = () => {
    selectYear.value = date.value.getFullYear()
    selectMonth.value = date.value.getMonth() + 1
    selectDate.value = date.value.getDate()
  }
  watch(date, () => {
    openDatePicker.value = !openDatePicker.value
    getSelectDate()
  })
  onMounted(() => {
    getSelectDate()
  })
</script>
<template>
  <article id="intro" class="max-width article">
    <h1>카카오뱅크&#x1F4B0;<span>26주 적금</span> 계산기</h1>
    <TabMenu @emitTab="emitTab"/>
    <div class="pick-date">
      <ul class="depth1">
        <li class="join-date">
          <ul class="depth2">
            <li>
              <p>최초 가입일</p>
            </li>
            <li class="date-input">
              <div class="date-text" @click="openDatePicker = !openDatePicker">
                <p>{{ `${selectYear}-${selectMonth}-${selectDate}` }}</p>
                <span>&#x1F4C5;</span>
              </div>
              <div
                v-if="openDatePicker === true"
                class="date-picker">
                <DatePicker
                  @emitDate="emitDate" />
              </div>
            </li>
          </ul>
        </li>
        <li>
          <ul class="depth2">
              <li>
                <p>최고 금리</p>
                <p>23.04.19 기준</p>
              </li>
              <li class="benefit">연 7.00%</li>
          </ul>
        </li>
      </ul>
    </div>
  </article>
</template>
