<script setup>
import { ref, onMounted, defineEmits, defineProps, watch } from 'vue';
import util from '../../utils/util.mjs';

    const emits = defineEmits(['emitDate', 'emitMonth', 'emitClose'])
    const props = defineProps({
        list: Array,
        firstMonth: Number,
        lastMonth: Number,
        month: Number,
        year: Number
    })
    const today = ref(new Date())
    const days = ref(['일', '월', '화', '수', '목', '금', '토'])
    let thisYear = today.value.getFullYear()
    let thisMonth = today.value.getMonth()
    let monthlyArr = ref([])
    watch(
        () => props.month,
        () => {
            thisMonth = props.month - 1
            thisYear = props.year
            loadData()
        }
    )
    const loadData = () => {
        const arr = []
        const startDay = new Date(thisYear, thisMonth, 1)
        const endDay = new Date(thisYear, thisMonth + 1, 0)
        for (let i = 0; i < endDay.getDate(); i+= 1) {
            const day = (startDay.getDay() + i) % 7
            arr.push({
                date: i + 1,
                day
            })
        }
        if (startDay.getDay() > 0) {
            const num = Number(startDay.getDay())
            for (let j = 0; j < num; j += 1) {
                arr.unshift({
                    date: null,
                    day: num - (j + 1)
                })
            }
        }
        if (endDay.getDay() < 6) {
            const num = Number(6 - endDay.getDay())
            for (let h = 0; h < num; h += 1) {
                arr.push({
                    date: null,
                    day: h + 1
                })
            }
        }
        monthlyArr.value = arr
    }

    const nextMonth = () => {
        if (props.lastMonth && thisMonth + 1 === props.lastMonth) {
            return
        }
        if (thisMonth === 11) {
            thisMonth = 0
            thisYear++
        } else thisMonth++
        emits('emitMonth', thisMonth + 1)
        loadData()
    }
    const prevMonth = () => {
        if (props.firstMonth && thisMonth + 1 === props.firstMonth) {
            return
        }
        if (thisMonth === 0) {
            thisMonth = 11
            thisYear--
        } else thisMonth--
        emits('emitMonth', thisMonth + 1)
        loadData()
    }
    const emitMark = (date) => {
        if (props.month) {
            return
        }
        if (date) {
            const option = {
                mark: `${thisYear}-${thisMonth + 1}-${date}`,
                close: false
            }
            emits('emitDate', option)
        }
    }
    onMounted(() => {
        loadData()
    })
</script>
<template>
    <div id="datePicker">
        <div class="title">
            <p @click="prevMonth()" class="arrow"
            :class="{gray: props.firstMonth && thisMonth + 1 === props.firstMonth}">
                &#x25C0;
            </p>
            <p>{{ `${thisYear}.${thisMonth + 1}` }}</p>
            <p @click="nextMonth()" class="arrow"
            :class="{gray: props.lastMonth && thisMonth + 1 === props.lastMonth}">
                &#x25B6;
            </p>
        </div>
        <ul class="day-of-week">
            <li v-for="(day, dIdx) in days" :key="dIdx">
                {{ day }}
            </li>
        </ul>
        <ul class="day">
            <li  v-for="(dObj, dIdx) in monthlyArr" :key="dIdx"
            @click="emitMark(dObj.date)"
            :class="{on: props.list && props.list.findIndex((x) => new Date(x.date).getDate() === dObj.date) > -1}">
                <span>{{ dObj.date }}</span>
                <template v-if="props.list">
                    <p v-if="props.list.findIndex((x) => new Date(x.date).getDate() === dObj.date) > -1">
                        {{ util.priceKo(props.list.find((x) => new Date(x.date).getDate() === dObj.date).weeklyPay) }}
                    </p>
                </template>
            </li>
        </ul>
    </div>
</template>
